import React from 'react';
import './team.css';

const team = [
    { name: "Nevin Freeman", image: './images/team/ourteam-01.jpg' },
    { name: "Matt Elder", image: './images/team/ourteam-02.jpg'},
    { name: "Matt Gertler", image: './images/team/ourteam-03.jpg'},
    { name: "Josh Furnas", image: './images/team/ourteam-05.png' },
    { name: "Taylor Brent",image: './images/team/ourteam-06.jpg'},
    { name: "Charlie Smith",image: './images/team/ourteam-11.jpg' },
    { name: "Cathleen Kilgallen",image: './images/team/ourteam-12.jpg'},
    { name: "Jesper Ostman",image: './images/team/ourteam-17.jpg' },
    { name: "Erika Campbell",image: './images/team/ourteam-18.jpg' },
    { name: "Mark Lee",image: './images/team/ourteam-20.jpg'},
  ];
  export class Team extends React.Component {
    constructor() {
      super();
      this.state = {
        teamDetails: [
          {
            name: "Nevin Freeman",
            position: 'CO-FOUNDER AND CEO',
            details: 'Nevin oversees strategy, legal, and team coordination at Reserve.',
            details2: "Nevin is an entrepreneur who has co-founded three companies. His mission in life is to solve the coordination problems that are stopping humanity from achieving its potential, and he's particularly concerned about averting the long-term risks posed by the development of artificial intelligence.",  
          },
          {
            name: "Matt Elder",
            position: 'CO-FOUNDER AND CTO',
            details: 'Matt is designing, analyzing and overseeing the Reserve Stabilization Protocol and architecting the Reserve protocol implementation.',
            details2: 'Previously, he did engineering for Google, Quixey, and the Linux Standard Base(IBM); his graduate research at the University of Wisconsin-Madison was in program analysis.',
          },
          {
            name: "Matt Gertler",
            position: 'GENERAL COUNSEL',
            details: 'Matt is developing Reserve’s legal strategy.',
            details2: 'Previously Matt co-founded Digital Asset Research, and worked at Milbank, Promontory Financial Group, Earnest, and Venmo. He has a J.D./M.B.A. from USC and a B.A. in Economics and History from UCLA.',
          },
          {
            name: "Josh Furnas",
            position: 'HEAD OF PRODUCT',
            details: 'Josh is designing the Reserve App that will allow users to access the protocol.',
            details2: 'Previously, he was the Director of Product Management at Credit Sesame and the co-founder of Selfless.is and The Big Moose Deli & Country Store.',
          },
          {
            name: "Taylor Brent",
            position: 'PROTOCOL DEVELOPMENT',
            details: 'Taylor is working on the design of the protocol.',
            details2: 'Previously he worked on analytical chemistry and bioinformatics infrastructure at Impossible Foods, and at Google doing data engineering.',
          },
          {
            name: "Charlie Smith",
            position: 'BUSINESS DEVELOPMENT',
            details: 'Charlie is developing tactics and partnerships to get our protocol into the hands of users.',
            details2: 'Previously, he worked as a management consultant at ReD Associates, contributed to The Economist Magazine, and helped start a bitcoin mine in Texas.',
          },
          {
            name: "Cathleen Kilgallen",
            position: 'CFO AND COMPLIANCE OFFICER',
            details: 'Cathleen is managing Reserve’s finances and making sure the team is fully compliant.',
            details2: 'Previously, she was the COO at Leverage Research and Paradigm Academy. She has also performed various operational roles at the congressional office of former Rep. Dennis Kucinich, The Jane Goodall Institute, and Amnesty International.',
          },
          
          {
            name: "Jesper Ostman",
            position: 'PROTOCOL DEVELOPMENT',
            details: "Jesper is designing the Reserve Stabilization protocol. He's also analyzing the stablecoin competitor landscape.",
            details2: 'He has spent 4 years researching methodology for the Institute for Philosophical Research, after doing a theoretical philosophy Phd at Umeå University.',
          },
          
          {
            name: "Erika Campbell",
            position: 'ONSITE OPERATIONS',
            details: 'Erika is the point person of physical operations for Reserve.',
            details2: 'She previously managed operations for Leverage Research and Paradigm Academy. She also has several years of softball coaching experience at the Division I and Division II collegiate levels',
          },
          {
            name: "Mark Lee",
            position: 'LEGAL',
            details: 'Mark is doing legal research for Reserve.',
            details2: 'He has a J.D. from Harvard Law School and previously worked for a major law firm in NY in the global securities practice group.',
          }
        ],
        filterteamDetail: []
      };
    }
  
  
    handleClick = name => {
      let filterteamDetail = [];
      if (name === "All") {
        filterteamDetail = this.state.teamDetails;
      } else {
        filterteamDetail = this.state.teamDetails.filter(
            teamDetail => teamDetail.name === name
        );
      }
  
      this.setState({ filterteamDetail });
    };
  
    render() {
      const renderAll = this.state.filterteamDetail.map(teamDetail => (
          <div className = 'details'>
        <h2 className='text-3xl' key={teamDetail.name}>{teamDetail.name}</h2>
        <h3 className='text-base md:text-xl'>{teamDetail.position}</h3>
        <p className='md:text-2xl pb-2 lg:text-lg'>{teamDetail.details}</p>
        <p className='md:text-2xl md:leading-10 lg:text-lg'>{teamDetail.details2}</p>
        </div>
      ));
      return (
        <div className="team flex w-full flex-wrap justify-between flex-col overflow-hidden lg:flex-row ">
          <div className='team-image flex flex-wrap pl-5 order-last w-full lg:order-none lg:mt-32 lg:w-4/6'>
          {team.map(({ image, name }) => (
            <div
              key={name}
              onMouseOver={this.handleClick.bind(this, name)}
              className='img  flex flex-wrap justify-around flex-row cursor-pointer'
            >
              <img className = "inline-block" src= {`${image}`} alt="" width= '159.85' height= '159.85'/>
            </div>
          ))}
          </div>
        <div className='team-detail w-full text-center lg:w-2/6'>
            <h1 className='title uppercase text-3xl mt-5 mb-3 md:text-5xl'>meet the team </h1>
            <div>
              <a href="https://reserve.org/our-team" className="button w-button">view all</a>
              <a href="https://reserve.org/about#join-us" className="button w-button">join our team</a>
            </div>
            <p className="text-left">{renderAll}</p>
        </div>
          
        </div>
      );
    }
  }