import React from 'react';

export function Next() {
    return (
        <div className='vision-cont px-2 leading-7 font-sans w-full'>
            <div className='vision'>
                <h1 className="text-center font-bold text-4xl py-8 md:text-5xl">
                    What next?  
                </h1>
                <div className='columns md:text-2xl lg:text-base'>
                    <div className='column-cont lg:flex flex-row max-w-full'>
                        <div className ='column-para1 lg:w-2/4 lg:mr-3'>
                            <p>Cryptocurrency started as a technical discussion on a mailing list, grew into a small movement, attracted hordes of speculators, and then splintered into a zillion useless imitations of itself. We believe it’s destined to consolidate and eventually give way to a battle for power on the global stage. We foresee uncomfortable disruption, a real threat of catastrophe, and a real promise of increased prosperity. Let us try to explain why.</p>
                            <p className='py-3'>Money is broken in some parts of the world – corrupt governments have a hard time maintaining stable value in their currencies.</p>
                        </div>
                        
                        <div className='column-para2 lg:w-2/4'>
                            <p>Centralized, dollar-backed cryptocurrencies are an excellent solution to this problem, offering an alternative to citizens of high-inflation countries, but we worry that these solutions won’t last.</p>
                            <p className='py-3'>We believe the future dominant cryptocurrency will be a decentralized, dollar-independent stablecoin, and it will change the economic situation in many countries around the world.</p>

                            <a className='px-5 underline hidden lg:block text-lg md:text-2xl lg:w-full' href="https://reserve.org/our-vision">Learn more about our vision here</a>
                        </div>
                        
                    </div>
                    
                    <a className='px-5 underline text-base md:text-2xl lg:hidden' href="https://reserve.org/our-vision">Learn more about our vision here</a>
                </div>
            </div>
        </div>
    )
}

