import React from 'react';
import ReactDOM from 'react-dom';
import './slide.css'


const colors = [
  {
    title: 'Reserve is a flexible pool of stablecoins',
    subtitle: 'Designed to reduce risk through diversification and decentralized governance.',
    subsm1: 'Designed to reduce risk through diversification',
    subsm2: 'and decentralized governance.',
    imageUrl: './images/RS_Web.png',
    id: 1,
  },
  {
    title: 'Easily Accept Payments',
    subtitle: 'Reserve allows businesses to accept payments in a stable digital currency, ',
    subtitle2: 'enabling them to easily pay their international suppliers, while also keeping ',
    subtitle3: 'their money out of inflation-prone local currency.',
    subsm1: 'Reserve allows businesses to accept payments in a stable ',
    subsm2: 'digital currency, enabling them to easily pay their ',
    subsm3: 'international suppliers, while also keeping their money ',
    subsm4: 'out of inflation-prone local currency.',
    imageUrl: './images/payments.png',
    id: 2,
  },
  {
    title: 'Protect Your Money',
    subtitle: 'Reserve enables people and businesses in countries with high inflation to protect their ',
    subtitle2: 'money by moving it out of broken currencies into a stable digital currency.',
    subsm1: 'Reserve enables people and businesses in countries with',
    subsm2: 'high inflation to protect their money by moving it out of ',
    subsm3: 'broken currencies into a stable digital currency.',
    imageUrl: './images/umbrella.jpg',
    id: 3,
  },
  {
    title: 'Send Money Across Borders',
    subtitle: 'Reserve makes it cost-effective to send money across borders. This enables people to ',
    subtitle2: 'send money home to their families, and businesses to pay international suppliers ',
    subtitle3: 'in a stable currency.',
    subsm1: 'Reserve makes it cost-effective to send money across ',
    subsm2: 'borders. This enables people to send money home to their ',
    subsm3: 'families, and businesses to pay international suppliers ',
    subsm4: 'in a stable currency.',
    imageUrl: './images/send.png',
    id: 4,
  },
  {
    title: 'Two tokens power the Reserve protocol',
    subtitle: '',
    imageUrl: './images/one_one.png',
    id: 5,
  }
];

const delay = 7000;

function Slideshow() {
  const [index, setIndex] = React.useState(0);
  const timeoutRef = React.useRef(null);

  function resetTimeout() {
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
    }
  }

  React.useEffect(() => {
    resetTimeout();
    timeoutRef.current = setTimeout(
      () =>
        setIndex((prevIndex) =>
          prevIndex === colors.length - 1 ? 0 : prevIndex + 1
        ),
      delay
    );

    return () => {
      resetTimeout();
    };
  }, [index]);

  return (
    <div className="slideshow mr-auto overflow-hidden max-w-full mt-36 lg:mt-32 md:mx-auto md:w-4/5 lg:w-4/5 xl:w-3/5">
      <div
        className="slideshowSlider whitespace-nowrap transition duration-1000 ease-in w-full"
        style={{ transform: `translate3d(${-index * 100}%, 0, 0)` }}
      >
        {colors.map((colors, id) => (
          <div
            className="slide inline-block bg-center bg-cover bg-no-repeat object-cover h-72 w-screen md:w-full md:h-96 lg:h-full"
            key={id}
            style={{ backgroundImage: `url(${colors.imageUrl})`}}
          > <div className= "subs bg-black bg-opacity-75 text-white absolute bottom-0 p-2 w-full">
              <h1 className='uppercase font-bold text-base lg:text-2xl'>{colors.title}</h1>
              <div >
                <p className = ' hidden md:block'>{colors.subtitle}</p>
                <p className = ' hidden md:block'>{colors.subtitle2}</p>
                <p className = ' hidden md:block'>{colors.subtitle3}</p>
                <p className = 'text-xs md:hidden'>{colors.subsm1}</p>
                <p className = 'text-xs md:hidden'>{colors.subsm2}</p>
                <p className = 'text-xs md:hidden'>{colors.subsm3}</p>
                <p className = 'text-xs md:hidden'>{colors.subsm4}</p>
                </div>
                </div>
                </div>
         
        ))} 
      </div>

      <div className="slideshowDots text-center">
        {colors.map((_, idx) => (
          <div
            key={idx}
            className={`slideshowDot${index === idx ? " active" : ""} inline-block h-2 w-2 rounded-2xl cursor-pointer bg-gray-300 mt-4 mr-2 mb-0`}
            onClick={() => {
              setIndex(idx);
            }}
          ></div>
        ))}
      </div>
    </div>
  );
}


ReactDOM.render(<Slideshow />, document.getElementById("root"));

export default Slideshow;