import React from 'react'
// import './nav.css'

function Nav() {
  return (
    <div className = " w-full fixed top-0 overflow-hidden z-50 bg-white">
    <div className = "flex justify-between sm:sticky max-w-full p-3">
      
      <div className = 'logo pt-3'>
        <a href="/#" >
            <img className='lg:w-48 lg:p-2' src="./images/Reserve_Logo.png" alt="" width="120px" height="25px"/>
        </a>

      </div>

      <div className = "price overflow-hidden mx-3 w-26 h-10 lg:w-38 lg:h-24 ">
      <div className="coinmarketcap-currency-widget transform  relative bottom-9 right-20 pr-2 lg:top-5 lg:left-9 lg:scale-150" data-currencyid="3964" data-base="USD" data-secondary="" data-ticker="false" data-rank="false" data-marketcap="false" data-volume="false" data-statsticker="false" data-stats="USD"></div>
      </div>


      <div className="nav-links hidden lg:flex justify-between pt-4">
                    <a className='p-3 no-underline uppercase tracking-widest text-black text-base hover:text-blue-700' href="https://reserve.org/about">the project</a>
                    <a className='p-3 no-underline uppercase tracking-widest text-black text-base hover:text-blue-700' href="https://reserve.org/protocol">the protocol</a>
                    <a className='p-3 no-underline uppercase tracking-widest text-black text-base hover:text-blue-700' href="https://reserve.org/ethics">ethics</a>
                    <a className='p-3 no-underline uppercase tracking-widest text-black text-base hover:text-blue-700' href="https://medium.com/reserve-currency">blog</a>
                    <a className='p-3 no-underline uppercase tracking-widest text-black text-base hover:text-blue-700' href="https://forum.reserve.org">forum</a>
      </div>

      

      <div className = 'menu lg:hidden'>
        <a href="/#" >
        <i className="fa fa-bars text-black text-4xl max-w-min"></i>
        </a>
      </div>
      
    </div>
    
    </div>
  )
}

export default Nav
