import React from 'react';
import './whitepaper.css'

function Whitepaper() {
    return (
        <div className='whitepaper flex justify-around flex-col w-full overflow-hidden py-20 lg:flex-row'>

            <div><h2 className="tracking-wide font-bold text-center font-sans leading-tight mt-5 inline-block w-full text-base pl-4 lg:text-xl">The Reserve Protocol Explained at London Business School</h2>
            <div className = "laptop-container overflow-hidden relative right-28 top-10 w-full">
                
                <div className = 'laptop relative'>
                
                    <img className="w-full" src="./images/laptop.png" alt="" />
                
                </div>

                <div className = 'laptop-img' >
                    <a href="https://youtu.be/gHbkgM-KPLY" >
                        <img src="./images/laptop-img.jpg" alt=""/>
                    </a>
                </div>
            </div>
            </div>
            <div>
                
                <div className="protocol-blocks-right md:max-w-96 md:ml-auto md:h-max lg:w-96">
                    <h2 className="tracking-wide font-bold font-sans leading-tight mt-5 inline-block w-full text-xl pl-4 lg:text-2xl lg:mt-6">Download Reserve's Whitepaper</h2>
                    <h3 className="heading w-full inline-block my-2 text-base tracking-wider leading-relaxed uppercase font-bold font-sans pl-4">RESERVE&nbsp;stabilization&nbsp;PROTOCOL</h3>
                        <div className="video-text w-richtext ">
                            <p className = "w-screen p-1 md:w-full">Learn how the Reserve will achieve stability. Eventually, although not at launch, our protocol will back Reserve with a diversified pool of assets in a decentralized way. This is not light reading – if you are looking for a basic explanation, read our overview of <a href="https://reserve.org/protocol" data-rt-link-type="external">the protocol</a>.
                            </p>
                        </div>
                        <a href="https://reserve.org/whitepaper.pdf" target="_blank" rel="noopener noreferrer" className="button w-button m-4 mx-6">DOWNLOAd whitepaper</a>
                </div>
                
            </div>
            
        </div>
    )
}

export default Whitepaper
