import React from "react";
import PropTypes from "prop-types";
import './intro.css';

const Intro = ({ embedId }) => (
  <div className="w-full bg-black mt-40 pb-5 lg:p-0">
      <div className="video-responsive overflow-hidden w-3/4 max-w-screen mx-auto my-0 h-0 relative">
    <iframe
      width="853"
      height="480"
      src={`https://www.youtube.com/embed/${embedId}`}
      frameBorder="0"
      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
      allowFullScreen
      title="Embedded youtube"
      className = "absolute top-0 left-0 h-4/5 w-full mt-20"
    />
    </div>
  </div>
);

Intro.propTypes = {
  embedId: PropTypes.string.isRequired
};

export default Intro;
